import Vue from "vue"
import Router from "vue-router"
import Home from "@/views/homePage.vue"
import StandardIndex from "@/views/standard/standardIndex.vue"
Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home,
      meta: {
        title: "首页",
      },
      redirect: "/about",
      children: [
        {
          path: "/about",
          name: "about",
          meta: {
            title: "关于组件",
          },
          component: () => import("@/views/about/aboutIndex.vue"),
        },
        {
          path: "/theme",
          name: "theme",
          meta: {
            title: "自定义主题",
          },
          component: () => import("@/views/theme/themeIndex.vue"),
        },
        {
          path: "/form",
          name: "form",
          meta: {
            title: "表单",
          },
          component: () => import("@/views/layout.vue"),
          children: [
            {
              path: "/select",
              name: "select",
              meta: {
                title: "Select 选择器",
              },
              component: () => import("@/views/form/select/selectIndex.vue"),
            },
            {
              path: "/select1",
              name: "select1",
              meta: {
                title: "Select1 选择器",
              },
              component: () => import("@/views/form/select1/selectIndex.vue"),
            },
          ],
        },
      ],
    },
    {
      path: "/standard",
      name: "Standard",
      component: StandardIndex,
    },
    {
      path: "/",
      name: "*",
      component: Home,
    },
  ],
})
